<template>
  <div class="loginDiv" :class="login_status"> 
    <div class="login-box">
      <div class="container">
        <div class="row">
          <div class="col-12 text-center my-5">
            <img src="@/assets/images/logo.png" alt="">
          </div>
        </div>
        <div class="row">
          <div class="col-12 col-md-3 col-lg-4"></div>
          <div class="col-12 col-md-6 col-lg-4">
            <div class="row">
              <div class="col-12 mb-2">
                Account
                <input type="text" class="form-control" value="gigabyte\">
              </div>
              <div class="col-12 mb-2">
                Password
                <input type="password" class="form-control">
              </div>
              <div class="col-12">
                <button class="btn btn-danger btn-block mb-2" @click="userA">人員A 登入</button>
                <button class="btn btn-warning btn-block mb-2" @click="userB">人員B 登入</button>
                <button class="btn btn-success btn-block mb-2" @click="userC">人員C 登入</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    
  },
  computed: {
    login_status(){
      return this.$store.state.login_status
    }
  },
  watch: { 

  },
  mounted:function(){
    this.init()
  },
  data(){
    return {
      
    }
  },
  methods:{
    init(){
      // var _this = this;
      // setTimeout(function(){
      //   _this.$store.state.login_status = '';
      // },5000);
    },


    initStatus(_main_status,_mainId,_page_status,_pageId,_subpage_status,_subPageId,_subPageDataId){
      this.$store.state.main_status= _main_status;
      this.$store.state.mainId= _mainId;
      //次頁面
      this.$store.state.page_status= _page_status;
      this.$store.state.pageId= _pageId;
      //子頁面
      this.$store.state.subpage_status= _subpage_status;
      this.$store.state.subPageId= _subPageId;
      this.$store.state.subPageDataId= _subPageDataId;

      var _this = this;
      this.$store.state.loading_status = true;
      setTimeout(function(){
        _this.$store.state.login_status = '';
        _this.$store.state.loading_status = false;
      },3000);
    },

    userA(){
      this.initStatus('1', 0 , '',0,'',0,0);
    },
    userB(){
      this.initStatus('3', 0 , '',0,'active',0,0);
    },
    userC(){
      this.initStatus('2', 0 , 'active',0,'active',0,0);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .loginDiv{
    position: fixed;
    height: 100%;
    width: 100%;
    top: -100%;
    left: 0;
    z-index: 1000;
    background: linear-gradient(180deg,#212335,#4e505f,#212335);
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }
  .loginDiv.active{
    top: 0;
  }

  .login-box{
    position: absolute;
    width: 100%;
    top: calc(50% - 300px);
  }
</style>
